<template>
  <section>
    <div class="navbar-container box-shadow">
      <div class="container-fluid">
        <div class="navbar-header">
           <span class="page-title">报警规则详情</span>
        </div>
      </div>
    </div>
    <div class="content">
        <el-card class="background-fff card-table">
           <el-row :gutter="24" v-if="ruleData">
                <el-col :span="12">
                  <p><label>规则编号：</label><span>{{ruleData.id}}</span></p>
                  <p><label>所属产品名称：</label><span v-if="ruleData.product_type == 'bms'">裸金属（BMS）</span><span v-else>云服务器（CDS）</span></p>
                  <p><label>范围：</label><span>{{ruleData.trigger_symbol}}{{ruleData.value}}</span></p>
                  <p><label>沉默时间：</label><span>{{ruleData.silent_period_str}}</span></p>
                  <p><label>阈值：</label><span>{{ruleData.value}}</span></p>
                  <p><label>通知对象：</label><span v-for="(m, i) in ruleData.groupList" :key="i">{{m.name}}</span></p>
                  <p><label>关联资源：</label>
                    <span v-if="ruleData.scope == 1"><span v-for="(m, i) in instance_list" :key="i">实例名称:{{m.name}}</span></span>
                    <span v-else>
                      <span v-if="ruleData.product_type == 'cloud_vm'">全部云主机</span><span v-else>全部裸金属</span>
                    </span>
                  </p>
                </el-col>
                <el-col :span="12">
                  <p><label>规则名称：</label><span>{{ruleData.name}}</span></p>  
                  <p><label>报警状态：</label><span>{{ruleData.status_str}}</span></p>
                  <p><label>是否启用：</label><span v-if="ruleData.is_actvie == 0">已禁用</span><span v-else>已启用</span></p>
                  <p><label>有效时间：</label><span>{{ruleData.active_start_time}} 至 {{ruleData.active_end_time}}</span></p>
                  <p><label>规则描述：</label><span>{{ruleData.period_str}} {{ruleData.lasted_period_str}} {{ruleData.metrics_id_str}} {{ruleData.trigger_type_str}} {{ruleData.trigger_symbol}}{{ruleData.value}}{{ruleData.unit}} 则发"{{ruleData.level_str}}"报警</span></p>
                </el-col>
            </el-row>  
        </el-card>
    </div>
  </section>  
</template>
<script>
import { getRulesList,linkInstance } from "@/service/alarm/alarm_rules";
import ElCard from "@/components/card";
import { Message } from "element-ui";
export default {
  components: { ElCard },
  data() {
    return {
      ruleData: null,
      instance_list: []
    };
  },
  created() {
    this.FnGetData()
    this.FnGetLinkInstance()
  },
  methods: {
    async FnGetData() {
      let reqData = {
        rule_id: this.$route.params.id
      };
      let res = await getRulesList(reqData);
      if (res.code === "Success") {
        this.ruleData = res.data[0];
        let keyList = [];
        let groupList = [];
        keyList = Object.keys(this.ruleData.notification_group);
        keyList.forEach(m => {
          groupList.push({
            id: m,
            name: this.ruleData.notification_group[m]
          })
        })
        this.ruleData.groupList = groupList;
      } else {
        Message({ message: res.msg, type: "error" });
      }
    },
    async FnGetLinkInstance() {
      let reqData = {
         rule_id: this.$route.params.id,
         page_number: 1,
         page_size: 100000
      }
      let res = await linkInstance(reqData);
      if (res.code === 'Success') {
        this.instance_list = res.data;
      } else {
        Message({message: res.msg, type: 'error'});
      }
      
    }
  }
};
</script>

<style>
.card-table p label {
  color: #7f86a5;
  font-size: 14px;
}
.card-table p span {
  font-size: 14px;
  margin: 0 10px;
}
</style>
